:root {
  --font-title: "Montserrat", sans-serif;
  --font-family: "Inter", sans-serif;
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light: #fff;
  --body: #64696e;
  --primary: #37BE75;
  --secondary: #002e6d;
  --darken: #001c41;
  --lighten: #19427b;
  --meta: #505458;
  --bg-color: #f9f9f9;
}

