.header {
  width: 100%;
  position: fixed;
  z-index: 5;
  padding: 0 0 0 50px;
  background-color: transparent;
  transition: all 0.2s linear;
}
.header__hide {
  transform: translateY(-100px);
}
.header__light {
  background-color: var(--light);
  box-shadow: 0px 5px 30px rgba(0, 46, 109, 0.2);
}
.header__light .nav__link {
  color: var(--secondary);
}
.header__light .nav__link:hover::before {
  width: 100%;
  background-color: var(--secondary);
}
.header__container {
  display: flex;
  align-items: center;
  height: 75px;
}
.header__icon {
  display: block;
  width: 200px;
  height: 50px;
  fill: var(--primary);
}
.header__phone {
  height: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 16px;
  background-color: var(--primary);
  color: var(--light);
  font-weight: 700;
  padding: 15px 20px;
}
.header__icon--phone {
  display: block;
  width: 25px;
  height: 25px;
  fill: var(--light);
}
.header__social {
  margin-right: 20px;
}

.nav {
  margin: 0 auto;
}
.nav__list {
  display: flex;
}
.nav__item:not(:last-child) {
  margin-right: 25px;
}
.nav__link {
  color: var(--light);
  font-family: var(--font-title);
  font-weight: 600;
  font-size: 16px;
  position: relative;
  padding: 5px 10px;
}
.nav__link::before {
  position: absolute;
  content: "";
  width: 0%;
  height: 2px;
  bottom: -5px;
  left: 0px;
  transition: all 300ms linear 0ms;
}
.nav__link:hover::before {
  width: 100%;
  background-color: var(--light);
}

.social {
  display: flex;
}
.social__item:not(:last-child) {
  margin-right: 15px;
}
.social__icon {
  display: block;
  width: 35px;
  height: 35px;
  fill: var(--primary);
}

.swiper--hero {
