}

.hero {
  margin-bottom: 100px;
}
.hero__container {
  width: 100%;
}
.hero__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5;
  color: var(--light);
}
.hero__wrapp {
  max-width: 800px;
}
.hero__slide::after, .hero__slide::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}
.hero__slide::before {
  z-index: 1;
  background-color: #001c41;
  opacity: 0.25;
}
.hero__slide::after {
  z-index: 2;
  opacity: 0.75;
  background: linear-gradient(to bottom, #040f1b 0%, #08111a 20%, rgba(19, 22, 26, 0) 65%, rgba(27, 26, 26, 0) 100%);
}
.hero__img {
  width: 100%;
  height: 100%;
}
.hero__title {
  margin-bottom: 15px;
}
.hero__subtitle {
  margin-bottom: 15px;
}
.hero__wrapp-btn {
