  margin-left: calc((100vw - 1310px) / 2);
}

.card {
  display: flex;
  flex-direction: column;
  height: 100%;
  border-radius: 5px;
  box-shadow: 5px 5px 12px 0px rgba(0, 0, 0, 0.2509803922);
}
.card__desc-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  font-size: 16px;
  margin: auto 0;
}
.card__img {
  width: 100%;
  height: 300px;
  border-radius: 5px 5px 0 0;
}
.card__content {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px 20px;
}
.card__title {
  margin-bottom: 10px;
}
.card__desc {
  margin-bottom: 10px;
}
.card__btn {
  justify-content: center;
  padding: 10px 40px;
  margin-top: 10px;
}

.about {
  margin-bottom: 100px;
}

.promo {
  margin-bottom: 100px;
}
.promo__wrapp {
  display: flex;
  height: 700px;
}
.promo__left {
  display: flex;
  flex-direction: column;
  background-color: var(--primary);
  color: var(--light);
  padding: 40px 60px;
  border-radius: 5px;
  width: 40%;
  z-index: 1;
}
.promo__right {
  margin-left: -10px;
  width: calc(60% + 10px);
}
.promo__img {
  height: 100%;
  width: 100%;
}
.promo__title {
  margin-bottom: auto;
}
.promo__subtitle {
  margin-bottom: 40px;
}
.promo__btn {
  margin: 0 auto 0 0;
}

.gallery {
  margin-bottom: 100px;
  padding: 0 0 50px 0;
  background: linear-gradient(0deg, rgb(0, 46, 109) 40%, rgba(0, 46, 109, 0) 80%);
}
.gallery__slide {
  position: relative;
  height: 450px;
}
.gallery__card {
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.gallery__card:hover .gallery__description {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}
.gallery__card:hover .gallery__img {
  transform: scale(1.1);
}
.gallery__img {
  height: 100%;
  width: 100%;
  transition: all 300ms linear 0ms;
  transform: scale(1);
  transition-property: transform;
}
.gallery__description {
  position: absolute;
  top: 50px;
  left: 50px;
  visibility: hidden;
  opacity: 0;
  background-color: var(--light);
  padding: 20px 25px;
  transform: translateX(100px);
  transition: all 300ms linear 0ms;
  border-radius: 5px;
}
.gallery__bottom {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
  color: var(--light);
  border-radius: 5px;
}
.gallery__form {
  display: flex;
  flex-direction: column;
}

.swiper--gallery {
  overflow: visible;
  margin-bottom: 50px;
}

.service {
  margin-bottom: 100px;
}
.service__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 250px);
  gap: 30px;
  grid-template-areas: "a b b" "c d e" "c f g";
  margin-bottom: 30px;
}
.service__item {
  position: relative;
  padding: 25px;
  border-radius: 5px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.service__item.a {
  grid-area: a;
}
.service__item.b {
  grid-area: b;
  background-image: url(../img/banner/img-1.webp);
}
.service__item.c {
  grid-area: c;
  background-image: url(../img/banner/img-2.webp);
}
.service__item.d {
  grid-area: d;
  background-image: url(../img/banner/img-5.webp);
}
.service__item.e {
  grid-area: e;
  background-image: url(../img/banner/img-6.webp);
}
.service__item.f {
  grid-area: f;
  background-image: url(../img/banner/img-4.webp);
}
.service__item.g {
  grid-area: g;
  background-image: url(../img/banner/img-9.webp);
}
.service__desc {
  display: inline-flex;
  background-color: var(--light);
  color: var(--secondary);
  padding: 10px 15px;
  border-radius: 5px;
  z-index: 5;
  position: absolute;
}
.service__bottom {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
  background-color: var(--primary);
  color: var(--light);
  padding: 25px;
  border-radius: 5px;
}
.service__form {
  display: flex;
  flex-direction: column;
}
.service__item:not(:first-child):after {
  content: "";
  opacity: 0.5;
  background: linear-gradient(to bottom, #040f1b 0%, #08111a 20%, rgba(19, 22, 26, 0) 65%, rgba(27, 26, 26, 0) 100%);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  border-radius: 5px;
}

.form__input {
  width: 100%;
  border-radius: 5px;
  padding: 15px 15px;
  font-size: 16px;
  margin-bottom: 15px;
  font-family: var(--font-family);
  font-weight: 600;
  background-color: var(--light);
  border: 1px solid #fff;
  color: var(--secondary);
  transition: all 300ms linear 0ms;
}
.form__input:hover {
  border: 1px solid var(--secondary);
}
.form__input:focus {
  outline: none;
  border: 1px solid var(--secondary);
}
.form ::placeholder {
  color: var(--secondary);
}
.form__btn {
  margin: 0 auto 0 0;
  padding: 15px 40px;
}

.stage {
  margin-bottom: 100px;
  padding: 80px 0;
  background-image: url(../img/banner/img-4.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}
.stage::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.7;
  background-color: rgb(0, 46, 109);
}
.stage__title {
  color: var(--light);
  margin-bottom: 25px;
}
.stage__grid {
  position: relative;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 30px;
}
.stage__desc {
  grid-column: 1/span 2;
  padding: 0 60px 40px 60px;
}
.stage__pagination {
  position: static;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
}
.stage__pagination-item {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  font-family: var(--font-title);
  font-size: 22px;
  width: 110px;
  height: 50px;
  color: var(--light);
  background-color: transparent;
  padding: 10px;
  margin: 0 !important;
  opacity: 1;
  transition: all 300ms linear 0ms;
}
.stage__pagination-item::before {
  content: "";
  position: absolute;
  width: 55%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--primary);
  border-radius: 5px;
  z-index: -1;
  transition: all 300ms linear 0ms;
}
.stage__pagination-item.swiper-pagination-bullet-active {
  color: var(--primary);
}
.stage__pagination-item.swiper-pagination-bullet-active .stage__pagination-text {
  color: var(--primary);
}
.stage__pagination-item.swiper-pagination-bullet-active::before {
  background-color: var(--light);
  width: 100%;
}
.stage__pagination-item:hover {
  color: var(--primary);
}
.stage__pagination-item:hover .stage__pagination-text {
  color: var(--primary);
}
.stage__pagination-item:hover::before {
  background-color: var(--light);
  width: 100%;
}
.stage__pagination-text {
  position: absolute;
  font-size: 20px;
  font-family: var(--font-title);
  left: 45px;
  color: var(--light);
  transition: all 300ms linear 0ms;
}
.stage__swiper {
  position: relative;
  grid-column: 3/span 4;
  width: 100%;
}
.stage__slide {
  border-radius: 5px 0 0 5px;
}
.stage__slide:after {
  content: "";
  opacity: 0.5;
  background: -webkit-gradient(linear, left top, left bottom, from(#040f1b), color-stop(20%, #08111a), color-stop(65%, rgba(19, 22, 26, 0)), to(rgba(27, 26, 26, 0)));
  background: linear-gradient(to bottom, #040f1b 0%, #08111a 20%, rgba(19, 22, 26, 0) 65%, rgba(27, 26, 26, 0) 100%);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  border-radius: 5px;
}
.stage__slide.swiper-slide-active .stage__content {
  transform: translate(0px, 0px);
  opacity: 1;
}
.stage__content {
  position: absolute;
  top: 25px;
  left: 25px;
  background-color: var(--light);
  color: var(--primary);
  padding: 10px 25px;
  border-radius: 5px;
  z-index: 1;
  transform: translate(30px, 20px);
  opacity: 0;
  transition: all 500ms linear 0ms;
}

.advantages {
  margin-bottom: 100px;
}
.advantages__wrapp {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
}
.advantages__right {
  grid-column: 3/span 4;
  background-color: var(--primary);
  color: var(--light);
  padding: 40px 60px;
  border-radius: 5px;
  transform: scale(1.03);
}
.advantages__left {
  grid-column: 1/span 2;
  margin-left: -10px;
  width: calc(60% + 10px px);
}
.advantages__item:not(:last-child) {
  margin-bottom: 15px;
}
.advantages__card {
  background-color: rgba(255, 255, 255, 0.5019607843);
  border-radius: 5px;
  color: var(--secondary);
  background-size: 75px;
  background-repeat: no-repeat;
  padding: 25px 25px 25px 125px;
  background-position: 25px center;
}
.advantages__card:not(:last-child) {
  margin-bottom: 15px;
}
.advantages__card:nth-child(1) {
  background-image: url(../img/svg/10age.svg);
}
.advantages__card:nth-child(2) {
  background-image: url(../img/svg/snip.svg);
}
.advantages__card:nth-child(3) {
  background-image: url(../img/svg/camera.svg);
}
.advantages__card:nth-child(4) {
  background-image: url(../img/svg/control.svg);
}
.advantages__card:nth-child(5) {
  background-image: url(../img/svg/5age.svg);
}
.advantages__card:nth-child(6) {
  background-image: url(../img/svg/50home.svg);
}
.advantages__card:nth-child(7) {
