}

.pay {
  margin-bottom: 100px;
}
.pay__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 250px);
  gap: 50px;
}
.pay__card {
  position: relative;
  padding: 25px;
  border-radius: 5px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: var(--secondary);
}
.pay__card:first-child {
  background-image: url(../img/banner/img-1.webp);
}
.pay__card:nth-child(2) {
  background-image: url(../img/banner/card-1.webp);
}
.pay__card:last-child {
  background-image: url(../img/banner/img-9.webp);
  grid-column: 1/span 2;
}
.pay__card::before {
  content: "";
  opacity: 0.5;
  background: -webkit-gradient(linear, left top, left bottom, from(#040f1b), color-stop(20%, #08111a), color-stop(65%, rgba(19, 22, 26, 0)), to(rgba(27, 26, 26, 0)));
  background: linear-gradient(to bottom, #040f1b 0%, #08111a 20%, rgba(19, 22, 26, 0) 65%, rgba(27, 26, 26, 0) 100%);
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  border-radius: 5px;
  position: absolute;
}
.pay__desc {
  position: relative;
  display: inline-block;
  background-color: var(--light);
  border-radius: 5px;
  padding: 10px 15px;
}

.footer {
  background-color: var(--secondary);
  padding-top: 100px;
  padding-bottom: 100px;
}
.footer__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}
.footer__logo-icon {
  display: block;
  width: 250px;
  height: 60px;
  fill: var(--light);
}
.footer__contacts-wrapp {
  margin-left: auto;
}
.footer__contacts {
  margin-bottom: 15px;
}
.footer__contacts-item:not(:last-child) {
  margin-bottom: 5px;
}
.footer__link {
  color: var(--light);
  font-family: var(--font-title);
  font-weight: 600;
  font-size: 16px;
  position: relative;
  padding: 5px 0px;
}
.footer__title {
  color: var(--light);
  margin-bottom: 15px;
}
.footer__nav {
  margin: 0 auto;
}
.footer__nav-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 300px;
}
.footer__nav-item:not(:last-child) {
  margin-bottom: 10px;
}
.footer__nav-link {
  color: var(--light);
  font-family: var(--font-title);
  font-weight: 600;
  font-size: 16px;
  position: relative;
  padding: 5px 0px;
}
.footer__nav-link::before {
  position: absolute;
  content: "";
  width: 0%;
  height: 2px;
  bottom: -5px;
  left: 0px;
  transition: all 300ms linear 0ms;
}
.footer__nav-link:hover::before {
  width: 100%;
  background-color: var(--primary);
}